.form-container{
    background-color: #fafafa;
    padding-left: 5vw;
    padding-right: 5vw;
    padding-top: 5vw;
    }
.service-corner1,
.service-corner2,
.service-corner2,
.btn-container{
    margin: 0 auto; 
    padding: 20px 20px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow:  1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-box-pack: justify;
    -webkit-box-align: center;
}
.expense-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #4b4b4b;
}
.expense-item__description{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1rem;
}
.expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
}
.expense-item__price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;

}
.form-title{
    padding-left: 5vw;
}
.head1{
    font-size: 16px;
    color: rgb(208, 73, 73);
    padding-left: 12px;
}
.headtag1{
    font-size: 16px;
    padding-left: 12px;
    color: rgb(44, 147, 222);
}
.head2{
    line-height: 10px;
    font-weight: bold;
    font-family: 'robotoregular';
    font-size:14px;
}
.head3{
    line-height: 10px;
    font-family: 'robotoregular';
}
.head4{
    line-height: 10px;
    font-size: 16px;
    font-family: 'robotoregular';
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.paragraph{
    line-height: 10px;
    font-size: 14px;
    color: #9d9d9d;
}
.Button{
    height: 70px;
    width: 70px;
    background-color: blueviolet;
    border-radius: 50%;
    font-size: 26px;
    font-weight: bold;
    color: #fff;
}

@media (max-width:524px) {
    .c1{
        display: flex;
        flex-direction: row;
        height: 50vh;
        width: 50vw;
        background-color: #fff;
    }
    .expense-item__description{
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }
    .expense-item__description h2{
        font-size: 1.25rem;

    }
    .expense-item__price{
        font-size: 1.25rem;
    }
}
